          <div v-else>
            Publessing is a developer-friendly tool that lets you quickly draw web pages by typing commands. You can quickly complete a web page by assembling numerous pre-made parts. And the finished result can be downloaded as an html, css, js file
            and used according to the programming language and environment you are using.
          </div>
        </div>
        <div class="promotion part">
          <div class="sm-title">{{ store.state.site.lang === "ko" ? "홍보 영상" : "Promotional video" }}</div>
