            모바일 페이지만 필요한 경우, mobile 토글을 활성화하여 모바일용 컴포넌트를 사용하실 수 있습니다.
          </p>
          <p v-else>
            Most of the components support the responsive web, so you can use it in the environment of various devices such as desktop, tablet, and mobile.
